@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.pexeso-box {
    width: 150px;
    height: 150px;
    margin-bottom: 10px;
    border: 1px solid;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 5px 6px 8px -2px #0A0909;
    border-radius: 10px;
    /* background: linear-gradient(358deg, #e7f3d2, #60babf);
    background-size: 400% 400%;     */
/* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#6db3f2+0,54a3ee+50,3690f0+74,1e69de+100;Blue+Gloss+%233 */
    background: rgb(109,179,242); /* Old browsers */ /* FF3.6-15 */
    background: -webkit-radial-gradient(center, ellipse cover,  rgba(109,179,242,1) 0%,rgba(84,163,238,1) 50%,rgba(54,144,240,1) 74%,rgba(30,105,222,1) 100%); /* Chrome10-25,Safari5.1-6 */
    background: -webkit-radial-gradient(center,  ellipse,  rgba(109,179,242,1) 0%,rgba(84,163,238,1) 50%,rgba(54,144,240,1) 74%,rgba(30,105,222,1) 100%);
    background: radial-gradient(ellipse at center,  rgba(109,179,242,1) 0%,rgba(84,163,238,1) 50%,rgba(54,144,240,1) 74%,rgba(30,105,222,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#6db3f2', endColorstr='#1e69de',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}
.pexeso-box.hidden {
    opacity: 0;
    transition: opacity 0.3s;
    -webkit-transition: opacity 0.3s;
}
.pexeso-box:hover {
    box-shadow: none;
    position: relative;
    top: 1px;
    left: 1px;
    color: #ddd;
    -webkit-transition: color 1s;
    transition: color 1s;
}
.question {
    font-size: 45px;
}
