  font-family: "Roboto";
  font-size: 30px;
  border: 1px solid; 
  text-align: center;
  margin: 10px;
}
.footer {
  font-size: 15px;
  height: 20px;
}
.footer a:visited{
  color: #0000EE;
}
.box-list {
  display: flex;
  justify-content: space-evenly;
  align-content: space-between;
}
.box-row {
   margin-bottom: 15px;
}
.game-over {
  text-align: center;
  font-size: 35px;
  font-family: "Roboto";
}

button {
  display: inline-block;
  border: none;
  padding: 1rem 2rem;
  margin: 20px;
  text-decoration: none;
  background: #0069ed;
  color: #ffffff;
  font-family: sans-serif;
  font-size: 1rem;
  cursor: pointer;
  text-align: center;
  -webkit-transition: background 250ms ease-in-out, 
              -webkit-transform 150ms ease;
  transition: background 250ms ease-in-out, 
              -webkit-transform 150ms ease;
  transition: background 250ms ease-in-out, 
              transform 150ms ease;
  transition: background 250ms ease-in-out, 
              transform 150ms ease, 
              -webkit-transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
}

button:hover,
button:focus {
  background: #0053ba;
}

button:focus {
  outline: 1px solid #fff;
  outline-offset: -4px;
}

button:active {
  -webkit-transform: scale(0.99);
          transform: scale(0.99);
}
